@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500);
html,body {
    font-family: "Roboto", sans-serif;
    background: #F7F8FA;
    margin: 0;
    padding: 0;
    height:100%;
}
.above-content{
    position: absolute;
    top: 8em;
    left: 50%;
}
.float-container{
    position: relative;
}
.right{
    float: right;
}
.up{
    color:green;
}
.down{
    color:red;
}
.table-header{
    font-size: .9em;
    font-weight: 65;
}
.table-content{
    text-align: left;
}
.unit-lg {
    font-size: 1.5rem;
    line-height: .5;
 }
 
 .price-lg {
    font-size: 2.2rem;
    line-height: .8;
 }
.content-scroll{
  overflow: auto;
  min-height: 2em;
  padding-top: 70px;
  padding-bottom: 62px;
  overflow-x: hidden;
}
.wrapper{
  width: 100%;
  height: 100%;

}

.foot {
  bottom:0;
  position:fixed;
  width: 100%;
}
.head{
  width: 100%;
  top:0;
  position: fixed;
}
.pad-card{
  margin-top: .4em;
}
.center-content{
  position: absolute;
  top: 20em;
  left: calc(50% - 3.0em);
}
